.ChatHeaderLeft{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.ChatHeaderLeft__Profile{
    display: flex;
}
.ChatHeaderLeft__Profile > img{
    width: 40px;
    height: 40px;
}
.ChatHeaderLeft__Profile > h1{
    padding-left: 12px;
    font-size: 24px;
    font-weight: bold;
    margin-top: 5px;
}
.chat__Page__Icons{
    padding: 5px;
    background-color: #F5F5F5;
    margin-left: 15px;
    border-radius: 50%;
}