.Mess__Body__Chat__Row{
    margin-top: 10px;
}
.Search__Messenger{
    padding: 8px 16px 8px;
    display: flex;
    align-items: center;
    background-color: #F5F5F5;
    justify-content: center;
    border-radius: 50px;
    text-align: center;
    margin-left: 8px;
    color: #AAADB4;
    margin: 0 10px;
    height: 22px;
}
.Search__Messenger > .Search__Icons{

}
.Search__Messenger > .Search__Icons > path{
    fill: #AAADB4;
}
.Search__Messenger > input{
    width: 85%;
    background-color: #F5F5F5;
    border: 0px solid #F5F5F5;
    padding: 8px;
    font-size: 15px;
    color: #AAADB4;

}
.Search__Messenger > input:focus{
    outline: none;
}



/* Conversations */
.FB__User__Row{
    padding: 0;
    margin: 0;
    list-style: none;
}

.FB__User__Row h4{
    font-size: 15px;
    font-weight: 400;
}
.FB__User__Row p > span,
.FB__User__Row p{
    font-size: 13px;
    font-weight: 400px;
    color: rgba(153, 153, 153, 1);
}
.MesseagerUserCard__Data > li,
.FB__User__Row > li{
    display: flex;
    align-items: center;
    padding: 8px 2px;
    margin: 0 8px;
    
}
li.newRequests {
    margin-left: 3px;
}
.sms__Ions{    
    border-radius: 50%;
    margin: 0 10px;
    padding: 15px;
    background-color: #F5F5F5;
    width: 50px;
    height: 50px;
    border-radius: 10px;
}
