.Chat__App__Header {
    position: sticky;
    top: 0;
    left: 0;
    content: "";
    background: #fff;
    border-bottom: 1px solid #dedede;
}
.App__Header{
    display: flex;
    z-index: 99999;
}
.Header__LeftChat{
    flex: 0.24;
    padding: 10px 16px;
}
.Header__RightChat__Box{
    flex: 0.76;
    padding: 8px 8px 8px 16px;
    border-left: 1px solid #F5F5F5;
}
