.UserChatBox__NewMessage {
    display: flex;
    position: fixed;
    bottom: 0;
    justify-content: space-between;
    align-items: center;
    width: 49%;
    padding-top: 6px;
    padding-bottom: 6px;
    background-color: #fff;
}
.UserChatBox__NewMessage .inputArea {
    display: flex;
    align-items: center;
    background-color: #F2F2F2;
    padding: 5px 8px;
    border-radius: 50px;
}
.inputArea .ChatBox{
    height: 25px;
    width: 350px;
    background-color: #ff000000;
    border: 1px solid #ff000000;
    margin-left: 5px;
    margin-right: 5px;
    text-align: left;
}
.inputArea .ChatBox:focus{
    outline: none;
}
.UserChatBox__NewMessage path, .UserChatBox__NewMessage .inputArea  path{
    color: hotpink;
    font-size: large;
}