.UserCaht__BoxInfo{
    width: 100%;
}
.UserProfile{
    text-align: center;
    margin: 0 auto;
}
.UserProfile img{
    border-radius: 50%;
    padding: 14px 0;
}
.UserProfile h2{
    text-transform: capitalize;
}
.UserProfile p span,
.UserProfile p{
    color: rgba(0, 0, 0, .40);
    font-size: 14px;
    margin-bottom: 20px;
}
.UserChat__Privacy__Support,
.UserChat__MoreActions{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 10px;
    border-top: 1px solid #9e9e9e4d;
}
.UserCaht__BoxInfo path,
.UserCaht__BoxInfo h3{
    color: #9E9E9E;
    font-size: 16px;
    font-weight: 600;
}