.App__Header__Chat__Box{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.userCom__Img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.userCom__Title{
    font-weight: bold;
    font-size: 18px;
    margin-left: 10px;
}
.userCom__Icons{
    display: flex;
    align-items: center;
}
.userCom__con{
    padding: 5px;
    margin-left: 5px;
}
.userCom__con path{
    fill: #0084FF;
}
.userCom__con.def{
    margin-right: 0;
    background: #CCE6FF;
    border-radius: 50%;
}

