
.UserChatBox__ChatData{
    max-height: 78vh;
    overflow-y: scroll;
    margin-bottom: 65px;
}
.UserChatBox__ChatData::-webkit-scrollbar{
    width: 4px;
}
.UserChatBox__ChatData::-webkit-scrollbar-track:hover{
    background-color: #D9D9D9;
    border-radius: 50px;
}
.UserChatBox__ChatData::-webkit-scrollbar-thumb{
    background-color: #838383;
    border-radius: 50px;  
}
.UserChatBox__ChatData{
    display: flex;
    flex-direction: column;

}
.UserChatBoxMessage__Data{
    padding: 0 0 0 12px;
    z-index: -99;
}
.UserChatBoxNewMessage__Data{
    width: 100%;
    display: block;
}