
.Single__Time__Message{
    display: flex;
    position: relative;
}
.userImage__Message{
    position: absolute;
    bottom: 0;
}
.Single__Time__Message img{
    width: 28px;
    height: 28px;
    border-radius: 50%;
}
.Single__Time__Message ul li{
    padding: 0;
    margin-left: 40px;
    list-style: none;
    background-color: #F2F2F2;
    margin-bottom: 2px;
    font-size: 14px;
    padding: 6px 12px 7px;
    border-radius: 50px;
    text-align: left;
}