.App__Body{
display: flex;
}
.Body__LeftRow{
    flex: 0.254;
}
.Body__RightRow__Box{
    flex: 0.746;
}
.Body__LeftRow{
    max-height: 90vh;
    overflow-y: scroll;
}
.Body__LeftRow::-webkit-scrollbar{
    width: 8px;
    visibility: hidden;
}

.Body__LeftRow::-webkit-scrollbar:hover ::-webkit-scrollbar-thumb{
    visibility: visible;
}
.Body__LeftRow::-webkit-scrollbar-track:hover{
    background-color: #D9D9D9;
    border-radius:10px;
}
.Body__LeftRow::-webkit-scrollbar-thumb{
    background-color: #838383;
    border-radius:10px;
}
